<template>
    <div
        :style="wrapperStyle"
        class="animate-wrapper">

        <stage1
            :status="stageStatus2"
            @changeStatus="(status, scroll) => handleStatusChange(2, status, scroll)" />

        <div v-if="stageStatus2 === 3 || stageStatus2 === 0">
            <stage2
                :status1="stageStatus2"
                :status2="stageStatus3" />

            <stage3
                :status="stageStatus3"
                @changeStatus="status => handleStatusChange(3, status)" />

            <stage4
                :status="stageStatus4"
                @changeStatus="status => handleStatusChange(4, status)" />

            <stage5
                :status="stageStatus5"
                @changeStatus="status => handleStatusChange(5, status)" />

            <stage6
                :status="stageStatus6"
                @changeStatus="status => handleStatusChange(6, status)" />

            <stage7
                :status="stageStatus7"
                @changeStatus="status => handleStatusChange(7, status)" />

            <stage8
                :style="stageBgStyle"
                :status="stageStatus7" />

            <stage9
                :style="stageBgStyle"
                :status="stageStatus7"
                @reset="reset" />
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
import throttle from './utils';
import stage1 from './stage/stage1';
import stage2 from './stage/stage2';
import stage3 from './stage/stage3';
import stage4 from './stage/stage4';
import stage5 from './stage/stage5';
import stage6 from './stage/stage6';
import stage7 from './stage/stage7';
import stage8 from './stage/stage8';
import stage9 from './stage/stage9';

// 垂直屏数量 控制滚动距离
const ACTIVE_STAGE = 5;

// 总共的场景数 用来生成控制变量
const STAGE_NUMBER = 10;
const STAGE_PREFIX = 'stageStatus';

const keyMapping = [];
const keyObject = {};
for (let i = 0; i < STAGE_NUMBER; i++) {
    const key = STAGE_PREFIX + i;
    keyMapping[i] = key;
    keyObject[key] = 0;
}

export default {
    components: {
        stage1, stage2, stage3, stage4, stage5, stage6, stage7, stage8, stage9
    },
    data() {
        return {
            ...keyObject,

            step: 0,
            touchStartPos: 0,
            timer: null,
            idx: 0,
            canScroll: false
        };
    },
    computed: {
        ...mapState(['info']),
        wrapperStyle() {
            return {
                transform: `translateY(${-(this.step) / ACTIVE_STAGE * 100 + '%'})`,
                transition: 'transform 0.3s'
            };
        }
    },
    created() {
        document.body.addEventListener('touchstart', (e) => {
            if (this.canScroll) {
                this.touchStartPos = e.changedTouches[0].pageY;
            }
        });
        document.body.addEventListener('touchend', (e) => {
            if (this.canScroll) {
                const endPos = e.changedTouches[0].pageY;
                if (this.touchStartPos - endPos >= 80) {
                    this.canScroll = false;
                    this.nextScreen(true);
                }
            }
        });
        document.body.addEventListener('touchmove', (e) => {
            e.preventDefault();
        }, { passive: false });
        this.stageStatus2 = 1;
        this.stageStatus3 = 1;
    },
    methods: {
        handleStatusChange(idx, done = false, scroll = true) {
            const key = keyMapping[idx];
            this.idx = idx;
            // 触发企鹅出现箭头 滑动事件生效
            // 点击企鹅到箭头出来有4s延迟
            if (idx >= 3 && idx < 7 && !done) {
                this.timer = setTimeout(() => {
                    this.canScroll = true;
                }, 4000);
            }
            // 结尾动画三连
            if (idx === 7) {
                this.stageStatus2 = 0;
            }
            if (done) {
                this.canScroll = false;
                this.nextScreen(scroll);
            } else {
                this[key] = 2;
            }
        },
        // 该函数存在被频繁触发的风险 加一个节流
        // 由于每一屏的动画持续时间比较长 这里的设置长也没事
        nextScreen: throttle(function(scroll) {
            if (this.timer) {
                clearTimeout(this.timer);
                this.timer = null;
            }
            const key = keyMapping[this.idx];
            const nextKey = keyMapping[this.idx + 1];
            this[key] = 3;
            this[nextKey] = 1;
            if (scroll) {
                this.step++;
            }
        }, 500),
        // 再看一次
        reset() {
            keyMapping.forEach((key) => {
                this[key] = 0;
            });
            this.stageStatus2 = 1;
            this.stageStatus3 = 1;
            this.step = 0;
        }
    }
};
</script>
<style lang="less">
@import './animate.less';
@import './func.less';
@import './style/CG.less';
@import './style/common.less';
@import './style/stage1.less';
@import './style/stage2.less';
@import './style/stage3.less';
@import './style/stage4.less';
@import './style/stage5.less';
@import './style/stage6.less';
@import './style/stage7.less';
@import './style/stage8.less';
@import './style/stage9.less';

.animate-wrapper{
  position: relative;
  left: 0;
  top: 0;
}

.stage-wrapper{
  height: 100vh;
  &.disabled{
    .fadeOutFast();
  }
}
</style>
