<template>
    <div
        :class="stage2Cls">
        <img
            class="stage-2-line animate-state-2-bg-line"
            src="../../assets/stage2/stage-2-bg-line.png">
        <img
            class="stage-2-line animate-state-2-bg-line"
            src="../../assets/stage2/stage-2-bg-line.png">
        <img
            :class="{active: status1 === 3}"
            class="stage-2-penguin"
            src="../../assets/stage2/penguin.png">
    </div>
</template>
<script>
export default {
    props: {
        status1: {
            type: Number,
            default: 0
        },
        status2: {
            type: Number,
            default: 0
        }
    },
    computed: {
        stage2Cls() {
            let cls = 'stage-2';
            if (this.status2 !== 1) {
                cls += ' disabled';
            } else if (this.status1 === 3) {
                cls += ' active';
            }
            return cls;
        }
    }
};
</script>
