export default function throttle(func, wait) {
    let result = null;
    let timeout = null;
    let previous = 0;
    return function(...args) {
        const now = Date.now();
        if (!previous) previous = now;
        const remaining = wait - (now - previous);
        if (remaining <= 0 || remaining >= wait) {
            if (timeout) {
                clearTimeout(timeout);
                timeout = null;
            }
            previous = now;
            result = func.apply(this, args);
            if (!timeout) {
                args = null;
            }
        }
        return result;
    };
}
