export default {
    props: {
        status: {
            type: Number,
            default: 0
        }
    },
    computed: {
        penguinCls() {
            if (this.status === 0) {
                return '';
            }
            let cls = 'penguin';
            if (this.status === 1) {
                cls += ' blink';
            }
            if (this.status === 2) {
                cls += ' shake';
            }
            if (this.status === 3) {
                cls += ' disabled';
            }
            return cls;
        },
        arrowCls() {
            let cls = 'jiantou';
            if (this.status === 2) {
                cls += ' active';
            }
            if (this.status === 3) {
                cls += ' disabled';
            }
            return cls;
        }
    },
    methods: {
        nextStep() {
            this.$emit('changeStatus', false);
        },
        changeScreen() {
            this.$emit('changeStatus', true, false);
        },
        scrollScreen() {
            this.$emit('changeStatus', true);
        }
    }
};
