<template>
    <div
        :class="{hide: status === 3}"
        class="stage-1 stage-wrapper">
        <img
            :class="{finish: status === 3, active: status !== 0}"
            src="../../assets/stage1/screen0.jpg"
            class="screen0">
        <div
            :class="{active: status !== 0}"
            class="envelope-wrapper">
            <img
                :class="envelope1Cls"
                src="../../assets/stage1/envelope1.png">
            <img
                :class="envelope2Cls"
                src="../../assets/stage1/envelope2.png">
            <img
                :class="{active: status === 3}"
                src="../../assets/stage1/palm.png"
                class="palm">
            <img
                :class="{active: status === 2}"
                src="../../assets/stage1/btn01.png"
                class="btn">
            <div
                :class="{active: status === 2}"
                class="btn-click-wrapper"
                @click="changeScreen"/>
        </div>
        <div
            class="envelope-click-wrapper"
            @click="nextStep"/>
        <p
            :class="{disabled: status !== 1}"
            class="text">点开来看看吧...</p>
        <div
            :class="{active: status === 2}"
            class="text2 text-wrapper">
            <p>亲爱的王上，</p>
            <p class="p2">在过去的日子里，你使用的话费成功击败<span>99.99%</span>的用户，荣获： </p>
            <p class="p3">【话费自由】称号</p>
        </div>
        <div
            :class="{active: status === 2}"
            class="text3 text-wrapper">
            <p>现腾讯王卡4周年庆开始啦！</p>
            <p>快携带邀请函入场吧</p>
        </div>
        <div
            :class="{active: status === 3}"
            class="text4 text-wrapper">
            <p>啊？邀请函企鹅抢走了</p>
            <p>快抓住企鹅，拿回邀请函</p>
        </div>
        <div
            :class="{active: status === 3}"
            class="penguin-wrapper">
            <div class="penguin-blink">
                <img
                    src="../../assets/stage1/e.png"
                    class="p1">
                <img
                    src="../../assets/stage1/shou.png"
                    class="p2">
            </div>
            <!-- <img
                src="../../assets/stage1/penguin3.png"
                class="p3"> -->
            <img
                src="../../assets/stage1/penguin4.png"
                class="p4">
        </div>
        <img
            :class="{active: status === 3}"
            src="../../assets/stage1/penguin5.png"
            class="penguin5">
        <img
            :class="{active: status === 3}"
            src="../../assets/stage1/penguin6.png"
            class="penguin6">

        <div
            :class="{active: status === 3}"
            class="finish-white-circle"/>
    </div>
</template>
<script>
import mixins from '../mixins';

export default {
    mixins: [mixins],
    data() {
        return {
        };
    },
    computed: {
        envelope1Cls() {
            let cls = 'envelope1';
            if (this.status === 1) {
                cls += ' active';
            }
            if (this.status === 2) {
                cls += ' disabled';
            }
            if (this.status === 3) {
                cls += ' finish';
            }
            return cls;
        },
        envelope2Cls() {
            let cls = 'envelope2';
            if (this.status === 1) {
                cls += ' active';
            }
            if (this.status === 2) {
                cls += ' show';
            }
            if (this.status === 3) {
                cls += ' finish';
            }
            return cls;
        }
    }
};
</script>
